
import {
  BActions,
  BattleMapGlobalProps,
  BattleMapProps,
  BattleRoomUserProps
} from "./MapDataFormat";

// export { BattleMapGlobalProps, BattleRoomUserProps } from "./MapData"

export interface BattleChatMessageProps{
  from: string;
  to: string;
  message: string;
  time: number;
}

export enum MessageType{
  action = "action",
  chat = "chat",

  IupdateRoomSetting = "updateRoomSetting",
  IgetRoom = "getRoom",
  IuserChoose = "userChoose",
  IgetMap = "getMap",
  IprovideAI = "provideAI",
  ItimeoutBout = "timeoutBout",
  IgetRole = "getRole",

  OmapInfo = "mapInfo",
  OroomSetting = "roomSetting",
  Oresult = "result",
  Orole = "role",
  OgameBegin = "gameBegin",
}

export interface MessageAction {
  type: MessageType.action;
  data: BActions;
  flag: string;
}

export interface MessageChat {
  type: MessageType.chat;
  data: BattleChatMessageProps;
}

export interface MessageGetRole {
  type: MessageType.IgetRole;
  token: string;
}

export interface MessageRole {
  type: MessageType.Orole,
  flag: string| null,
  username: string,
  userid: string,
  headerUrl: string,
  map: BattleMapProps,
  nowBoutFlag: string
}

export interface MessageGetMap {
  type: MessageType.IgetMap;
}

export interface MessageResult {
  type: MessageType.Oresult;
  data: BattleMapProps;
  recordId: number;
}

export interface MessageProvideAI {
  type: MessageType.IprovideAI;
  flag: string;
  action?: BActions;
}

export interface MessageTimeoutBout {
  type: MessageType.ItimeoutBout;
}


//----------------------------------

export interface MessageGameBegin {
  type: MessageType.OgameBegin
}

export interface MessageUpdateRoomSetting {
  type: MessageType.IupdateRoomSetting
  globalSetting: BattleMapGlobalProps;
  /**
   * ...
   */
  usersInfo: BattleRoomUserProps[];
  isPrivate: boolean;
  published: boolean;
  ownerDsc: string;
  computers: string[];
}

export interface MessageRoomSetting{
  type: MessageType.OroomSetting
  globalSetting: BattleMapGlobalProps;
  usersInfo: BattleRoomUserProps[];
  isPrivate: boolean;
  published: boolean;
  ownerDsc: string;
}

export interface MessageUserChoose {
  type: MessageType.IuserChoose,
  flag: string;
  hero?: string;
  prepared?: boolean;
  token: string;
}

// export interface MessagePrepared{
//   type: MessageType.prepared;
//   flag: string;
// }

export interface MessageGetRoom{
  type: MessageType.IgetRoom
}

export interface MessageMapInfo{
  type: MessageType.OmapInfo,
  data: BattleMapProps
}

export type WsMessages =
  | MessageAction
  | MessageChat
  | MessageGetRole
  | MessageGetMap
  | MessageProvideAI
  | MessageResult
  | MessageRole
  | MessageTimeoutBout
  | MessageGameBegin
  | MessageUpdateRoomSetting
  | MessageUserChoose
  | MessageGetRoom
  | MessageRoomSetting
  | MessageMapInfo
  ;


