/**
 * ele geo props
 */
export interface BattleBuildProps {
  type: string;
  flag: string;
  posId: number;
}

/**
 * ele unit props
 */
export interface BattleUnitProps {
  type: string;
  blood: number;
  bullet: number;
  oil: number;
  supply: Record<number, number>;
  loadings: BattleUnitProps[];
  moved: boolean;
  isDiving: boolean;
  flag: string;
  occupied: number;
}

/**
 * interface：地图类型
 */
export interface BattleMapProps {
  geoLayer: Array<number>;
  buildLayer: Record<number, BattleBuildProps>;
  unitLayer: Record<number, BattleUnitProps>;

  sizeState: Record<"x" | "y" | "scale" | "width" | "height", number>;

  globalSetting: BattleMapGlobalProps;
  playerSetting: Record<string, BattleMapUserProps>;
}

export interface BattleMapGlobalProps {
  dailyMoney: number;
  cities: number;
  bouts: number;
  fogOpen: boolean;
  weather: string;
  aiLvl: string;
}

export interface BattleRoomSetting {
  dailyMoney: number;
  cities: number;
  bouts: number;
  fogOpen: boolean;
  weather: string;
  aiLvl: string;
}

export interface BattleMapUserProps {
  nowBout: number;
  flag: string;
  enemies: string[];
  taskBg: string;
  taskTarget: string;
  commanders: string[];
  nowMoney: number;
  isOver: boolean;
  grade: BattlePlayerGradeProps;
}

export interface BattleRoomUserProps {
  // flag: string;
  // enemies: string[];
  // taskBg: string;
  // taskTarget: string;
  // commanders: string[];
  // nowMoney: number;

  flag: string;
  nowCommander: string;
  userName?: string;
  userId?: string;
  /**
   * type
   */
  userHeaderUrl?: string;

  isComputer: boolean;
  prepared: boolean;
}

export interface BattlePlayerGradeProps {
  allUnitMoney: number;
  nowUnitMoney: number;
  lostUnitMoney: number;
  destroyUnitMoney: number;
}



/******************************************************************/
/******************************动作，action************************/
/******************************************************************/

export enum BActionType {
  wait = "wait",
  attack = "attack",
  load = "load",
  occupy = "occupy",
  supply = "supply",
  diving = "diving",
  buy = "buy",
  sortie = "sortie",
  boutEnd = "boutEnd",
  udpateMap = "updateMap"
}

export interface BactionUpdateMap {
  type: BActionType.udpateMap,
  geo?: number[];
  build?: Record<number, BattleBuildProps>;
  unit?: Record<number, BattleUnitProps>;
  playerInfo?: Record<string, BattleMapUserProps>;
}

export interface BActionWait {
  type: BActionType.wait;
  road: number[];
  restOil: number;
  encountered: boolean;
}

export interface BActionAtk {
  type: BActionType.attack;
  road: number[];
  restOil: number;
  restBullet: number;
  restBlood: number;

  targetId: number;
  targetRestBullet: number;
  targetRestBlood: number;
}

export interface BActionLoad {
  type: BActionType.load;
  road: number[];
  restOil: number;
}

export interface BActionSortie {
  type: BActionType.sortie;
  road: number[];
  restOil: number;
  childrenId: number;
  nowLoading: BattleUnitProps[];
}

/**
 * occuppy: 代表最终值
 */
export interface BActionOccupy {
  type: BActionType.occupy;
  road: number[];
  restOil: number;
  occupy: number;
}

export interface BActionDive {
  type: BActionType.diving;
  road: number[];
  restOil: number;
  diving: boolean;
}

// 连个特殊的action

export interface BActionSupply {
  type: BActionType.supply;
  /**
   * 暂约定最后这个
   */
  targetIds: number[];
  supply: Record<string, number>;
}

export interface BActionBuy {
  type: BActionType.buy;
  // typeName: typeof BattleUnitTypes[number],
  posId: number;
  unit: BattleUnitProps;
}

export interface BActinoBoutEnd {
  type: BActionType.boutEnd,
  nowFlag: string;
}

export type BActions =
  | BActionWait
  | BActionAtk
  | BActionOccupy
  | BActionLoad
  | BActionSortie
  | BActionDive
  | BActionSupply
  | BActionBuy
  | BActinoBoutEnd
  | BactionUpdateMap;
