export interface InfoBattleProps {
  battleName: string;
  battleId: string;
  authorId?: string; // author name
  authorName: string;
  battleDsc: string;
  createTime?: string;
  playCount?: number;
  version?: string;

  coverImage?: string;
  coverColor?: string;
  coverAudio?: string;

  mapList?: InfoTextProps[];
}

export interface InfoTextProps {
  textId: string;
  coverColor: string;
  coverImage: string;
  coverAudio: string;
  text?: string;
  title?: string;
  authorId?: string;
  mapId?: string;
}

export interface InfoMapProps {
  mapName: string;
  mapId: string;
  mapImage: string;

  version: string;
  type: string;
  kw: Array<string>;
  mapDsc: string;
  createTime: string;
  playCount: 20;
  authorId?: string;
  authorName: string;
}

export interface InfoRoomProps extends InfoMapProps {
  roomId: string;
  ownerId: string;
  ownerName: string;
  ownerDsc: string;

  restPerson: number;
  roomBeginTime: string;
  roomKey?: string;
}

export interface InfoMapSetProps {
  dailyMoney: number;
  cities: number;
  bouts: number;
  fogOpen: boolean;
  weather: string;
  aiLvl: string;
}

export interface InfoRoomUserProps {
  flag: string;
  enemies: string[];
  taskBg: string;
  taskTarget: string;
  commanders: string[];
  commanderUrls: string[];
  commanderPoint: number;
  nowCommander: string

  nowMoney: number;

  userName?: string;
  userId?: string;
  userHeaderUrl?: string;
  isComputer: boolean;
  prepared: boolean;
}


export interface InfoRaceProps extends InfoMapProps {
  raceId: string;
  ownerDsc: string;
  ownerId: string;
  visitors: number;
  bout: number;
  memebers: string[];
  raceBeginTime: string;
}


