export interface InfoResultProps {
  recordId: string;
  userId: string;
  userName: string;
  headerUrl: string;
  flag: string;

  allMoney: number;
  allMoney_r: number;
  lostMoney: number;
  lostMoney_r: number;
  destroyMoney: number;
  destoryMoney_r: number;
  rank: string[];
}

export interface InfoBlogProps {
  blogId: string;
  title: string;
  authorId: string;
  authorName: string;
  createTime: string;
  updateTime: string;
  kw: string[];
  dsc: string;
  good: number;
  copyright?: string
}

